.about {
  padding-top: 25px;
  padding-bottom: 25px;

  @include mq(desktop) {
    padding-bottom: 65px;
    padding-top: 65px;
  }
}

.about__title {
  margin-bottom: 20px;

  font-weight: 800;
  font-size: 20px;
  line-height: 1.3;
  letter-spacing: 0.03em;

  @include mq(tablet) {
    margin-bottom: 30px;
    font-size: 26px;
  }
  @include mq(desktop) {
    margin-bottom: 50px;
    font-size: 46px;
  }
}

.about__photo {
  display: flex;
  flex-direction: column;

  @include mq(tablet) {
    flex-direction: row;
    margin-bottom: 0;
  }
}

.about__photo-img {
  max-width: 243px;
  margin-bottom: 20px;

  @include mq(tablet) {
    margin-right: 43px;
    margin-bottom: 0;
  }
  @include mq(desktop) {
    max-width: 530px;
    margin-right: 30px;
  }
}

.about__info {
  display: flex;
  flex-direction: column;
  max-width: 480px;

  @include mq(tablet) {
    max-width: 402px;
  }
  @include mq(desktop) {
    max-width: 642px;
  }
}

.about__name {
  padding-bottom: 10px;

  font-weight: 800;
  font-size: 20px;
  line-height: 1.2;
  border-bottom: 1px solid var(--primay-text-color);

  @include mq(tablet) {
    padding-bottom: 10px;
    font-size: 24px;
  }
  @include mq(desktop) {
    padding-bottom: 24px;
    font-size: 37px;
  }
}

.about__list {
  margin-top: 25px;
  margin-bottom: 25px;

  // - горизонтальна риска знизу кнотейнера -
  &::after {
    content: '';
    display: flex;
    width: 20px;
    height: 2px;

    background-color: var(--brand-color);
    transition: color;
  }
}

.about__list-item {
  display: inline-flex;
  margin-bottom: 25px;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.2;
  letter-spacing: 0.06em;

  &::before {
    content: '';
    flex-shrink: 0;
    width: 17px;
    height: 14px;
    margin-right: 9px;
    background-size: contain;
    background-image: url(../images/icon/chaeck.svg);
    fill: var(--brand-color);

    @include mq(desktop) {
      width: 25px;
      height: 20px;
      margin-right: 35px;
    }
  }
  &:last-child {
    margin-bottom: 50px;
  }

  @include mq(tablet) {
    font-size: 12px;
  }
  @include mq(desktop) {
    font-size: 16px;
  }
}

.about__text {
  display: flex;
  flex-direction: column;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.67;
  color: var(--primay-text-color);

  @include mq(tablet) {
    font-size: 10px;
  }
  @include mq(desktop) {
    margin-bottom: 15px;
    font-size: 18px;
  }
}

